<template>
    <div>
        <h1 class="text-center">Registrar libro</h1>
        <v-form ref="form" v-model="valid" lazy-validation>
            <v-container>
                <v-row justify="center">
                    <v-col cols="12" md="9">
                        <v-text-field 
                            v-model="form.title" 
                            :rules="requiredRules" 
                            label="Nombre libro"
                            required>
                        </v-text-field>
                        <v-text-field 
                            v-model="form.author" 
                            :rules="requiredRules" 
                            label="Autor"
                            required>
                        </v-text-field>
                        <v-text-field 
                            v-model="form.image" 
                            :rules="requiredRules" 
                            label="SRC imagen"
                            required>
                        </v-text-field>

                        <v-select v-model="form.type" :items="types" :rules="[v => !!v || 'Item is required']" label="Tipo"
                            required></v-select>

                   

                        <v-btn :disabled="!valid" color="success" class="mr-4" @click="createBook">
                            Registrar
                        </v-btn>

                        <v-btn color="error" class="mr-4" @click="reset">
                            Limpiar formulario
                        </v-btn>

                       
                    </v-col>
                </v-row>
            </v-container>

        </v-form>
    </div>
</template>

<script>
import {db} from '@/firebase/firebase.js'
import {collection, addDoc} from 'firebase/firestore';
export default {
    name: 'Register-view',
    // props: {},
    data: function () {
        return {
            valid: true,
            form:{
                title:'',
                author:'',
                type:'',
                image:''
            },
            requiredRules:[
                v => !!v || 'Campo requerido',
            ],
            select: null,
            types: [
                'novela',
                'ciencia ficción',
            ],
           
        }
    },
    // computed: {},
    methods: {
        validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
      async createBook(){
        this.validate();

        if(!this.valid){
            return; //escapo de la función. 
        }

        try {
            
            let response = await addDoc(collection(db,'books'), this.form )
            console.log(response);
            //redirect
            this.$router.push('/');
        } catch (error) {
            console.log(error);
        }
      }
    }
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
}
</script>

<style scoped></style>