<template>
    <div>
        <h1 class="text-center">Libro {{idBook}}</h1>
        <v-form ref="form" v-model="valid" lazy-validation>
            <v-container>
                <v-row justify="center">
                    <v-col cols="12" md="9">
                        <v-switch
                        v-model="allowEdit"
                        label="Editar libro"
                        color="red"
                        ></v-switch>

                        <v-text-field 
                            v-model="form.title" 
                            :rules="requiredRules" 
                            label="Nombre libro"
                            :disabled="!allowEdit"
                            required>
                        </v-text-field>
                        <v-text-field 
                            v-model="form.author" 
                            :rules="requiredRules" 
                            label="Autor"
                            :disabled="!allowEdit"
                            required>
                        </v-text-field>
                        <v-text-field 
                            v-model="form.image" 
                            :rules="requiredRules" 
                            label="SRC imagen"
                            :disabled="!allowEdit"
                            required>
                        </v-text-field>

                        <v-select v-model="form.type" :items="types" :rules="[v => !!v || 'Item is required']" label="Tipo"
                            :disabled="!allowEdit" required></v-select>

                   

                        <v-btn :disabled="!allowEdit" color="success" class="mr-4" @click="editBook">
                            Editar
                        </v-btn>

                        <v-btn color="error" class="mr-4" @click="dialog=true">
                            Eliminar
                        </v-btn>

                        <v-btn color="error" class="mr-4" @click="reset">
                            Limpiar formulario
                        </v-btn>

                       
                    </v-col>
                </v-row>
            </v-container>
        </v-form>
        <v-dialog
            v-model="dialog"
            width="500"
        >
            

            <v-card>
                <v-card-title class="text-h5 grey lighten-2">
                    ¿Deseas eliminar el libro?
                </v-card-title>

               

                <v-divider></v-divider>

                <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="red" dark @click="deleteBook">
                    Eliminar
                </v-btn>
                <v-btn
                    color="primary"
                    text
                    @click="dialog = false"
                >
                    Cerrar
                </v-btn>
                </v-card-actions>
            </v-card>
    </v-dialog>
    </div>
</template>

<script>
import {db} from '@/firebase/firebase.js'
import {doc, getDoc, updateDoc, deleteDoc} from 'firebase/firestore';
export default {
    name: 'Register-view',
    props: {
        idBook:{
            type:String,
            required:true
        }
    },
    data: function () {
        return {
            valid: true,
            form:{
                title:'',
                author:'',
                type:'',
                image:''
            },
            requiredRules:[
                v => !!v || 'Campo requerido',
            ],
            select: null,
            types: [
                'novela',
                'ciencia ficción',
            ],
            allowEdit:false,
            dialog: false,
           
        }
    },
    // computed: {},
    methods: {
        validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
      async fetchBookById(){
        const docRef =  doc(db,'books',this.idBook)

        let response = await getDoc(docRef)
        console.log(response)
        console.log(response.data());
        this.form = response.data();
      },
   
        async editBook(){
            alert("aca")
            this.validate();

            if(!this.valid) return;

            try {
                let book = this.form;
                book.id = this.idBook;

                let response =  await updateDoc(doc(db,'books',book.id),book)
                console.log(response);
                //redirect
                this.$router.push('/')

            } catch (error) {
                console.log(error);
            }
            

        },
        async deleteBook(){
            try {
                let response = await deleteDoc(doc(db,'books', this.idBook))
                console.log(response);
                this.dialog = false;
                this.$router.push('/');
            } catch (error) {
                console.log(error);
            }
        }
    },
   
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    created(){
        this.fetchBookById()
    }
    // -- End Lifecycle Methods
}
</script>

<style scoped></style>